import { Fragment, useContext, useEffect, useLayoutEffect } from 'react'
import _ from 'lodash'
import Order from '../components/Order'
import OrderContext from '../context/OrderContext'
import Div from '../components/Div'
import Link from '../components/Link'
import { Button } from 'react-bootstrap'

const OrderList = () => {
  const { orders, setOrders, api, setNav, setHead, history } = useContext(
    OrderContext,
  )

  useEffect(() => {
    let nav = [
      <Link Class='icon-list' />,
      <Button
        type='submit'
        className='rounded border-white px-4 py-1'
        onClick={() => history.push('/')}>
        PUNCH OUT
      </Button>,
    ]
    let navbar = [nav, 'justify-content-between']
    let heading = ['', '']
    setNav(navbar)
    setHead(heading)
  })

  return (
    <Fragment>
      <div className='order-list'>
        {!_.isEmpty(orders) ? (
          orders.map((order) => <Order key={order.id} order={order} />)
        ) : (
          <p className='message'>No Orders Yet</p>
        )}
      </div>
    </Fragment>
  )
}

export default OrderList
